'use client'

import { useCallback, useEffect, useMemo, useState } from 'react'

import Alert from '@mui/material/Alert'
import Box from '@mui/material/Box'
import Card from '@mui/material/Card'
import CardContent from '@mui/material/CardContent'
import CircularProgress from '@mui/material/CircularProgress'
import Pagination from '@mui/material/Pagination'
import Stack from '@mui/material/Stack'
import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import TextField from '@mui/material/TextField'
import Typography from '@mui/material/Typography'

import { listarClientes } from '@/api/clientes'

const extractRows = payload => {
  if (Array.isArray(payload)) return payload
  if (Array.isArray(payload?.data)) return payload.data
  if (Array.isArray(payload?.rows)) return payload.rows
  if (Array.isArray(payload?.data?.rows)) return payload.data.rows

  return []
}

const extractPagination = payload => {
  const source = payload?.pagination || payload?.data?.pagination

  return {
    page: Number(source?.page || 1),
    pages: Number(source?.pages || 1),
    total: Number(source?.total || extractRows(payload).length)
  }
}

export default function ClientesModule() {
  const [clientes, setClientes] = useState([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')
  const [search, setSearch] = useState('')
  const [page, setPage] = useState(1)
  const [pagination, setPagination] = useState({ page: 1, pages: 1, total: 0 })

  const loadClientes = useCallback(async () => {
    setLoading(true)
    setError('')

    try {
      const response = await listarClientes({ page, limit: 20, search, estado: 'ACTIVO' })

      setClientes(extractRows(response))
      setPagination(extractPagination(response))
    } catch (err) {
      setError(err.message || 'No se pudo cargar clientes.')
    } finally {
      setLoading(false)
    }
  }, [page, search])

  useEffect(() => {
    const timeout = setTimeout(() => {
      loadClientes()
    }, 300)

    return () => clearTimeout(timeout)
  }, [loadClientes])

  useEffect(() => {
    setPage(1)
  }, [search])

  const rows = useMemo(() => clientes || [], [clientes])

  return (
    <Card>
      <CardContent>
        <Stack spacing={3}>
          <Box>
            <Typography variant='h4'>Clientes</Typography>
            <Typography color='text.secondary'>Listado de clientes activos.</Typography>
          </Box>

          <TextField
            label='Buscar cliente'
            placeholder='Nombre, apellido, teléfono o email'
            value={search}
            onChange={event => setSearch(event.target.value)}
            size='small'
          />

          {error ? <Alert severity='error'>{error}</Alert> : null}

          {loading ? (
            <Stack alignItems='center' py={8}>
              <CircularProgress size={28} />
            </Stack>
          ) : (
            <Table size='small'>
              <TableHead>
                <TableRow>
                  <TableCell>Nombre</TableCell>
                  <TableCell>Apellido</TableCell>
                  <TableCell>Email</TableCell>
                  <TableCell>Teléfono</TableCell>
                  <TableCell>Estado</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {rows.map(row => (
                  <TableRow key={row.id} hover>
                    <TableCell>{row.nombre || '-'}</TableCell>
                    <TableCell>{row.apellido || '-'}</TableCell>
                    <TableCell>{row.email || '-'}</TableCell>
                    <TableCell>{row.telefono || '-'}</TableCell>
                    <TableCell>{row.estado || '-'}</TableCell>
                  </TableRow>
                ))}
                {rows.length === 0 ? (
                  <TableRow>
                    <TableCell colSpan={5} align='center'>
                      Sin resultados
                    </TableCell>
                  </TableRow>
                ) : null}
              </TableBody>
            </Table>
          )}

          <Stack direction='row' justifyContent='space-between' alignItems='center'>
            <Typography color='text.secondary'>Total: {pagination.total}</Typography>
            <Pagination
              page={pagination.page}
              count={Math.max(pagination.pages, 1)}
              onChange={(_, value) => setPage(value)}
              size='small'
              color='primary'
            />
          </Stack>
        </Stack>
      </CardContent>
    </Card>
  )
}
